$color-primary: #12A755;
$color-dark: #121212;
$color-dark-lighter:#232323;
$color-light: #F3F3F3;

$color-white: #ffffff;

$font-family-Nunito-Sans: 'Nunito Sans',
sans-serif;

$font-size-normal: 16px;
$font-size-medium: 20px;
$font-size-large: 40px;
$font-size-x-large: 100px;

$font-family-base: $font-family-Nunito-Sans;

$grid-columns: 12 !default;
$grid-gutter-width: 48px !default;
$grid-row-columns: 6 !default;

$grid-breakpoints: (xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px) !default;

$container-max-widths: (sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1360px) !default;