.menu {
    position: absolute;
    z-index: 3;
    color: $color-primary;

    &__icon {
        position: absolute;
        left: 20px;
        top: 20px;
        cursor: pointer;
        width: 36px;
        height: 36px;
        font-size: 36px;

        z-index: 1;
    }

    &__content {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        left: 0;
        background-color: $color-dark-lighter;
        width: 400px;
        height: 100vh;
        padding: 76px;
        padding-top: 100px;

        transition: left 200ms ease-out;

        &--closed {
            left: -400px;
        }
    }

    &__navigation {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
    }

    &__navLink {
        text-transform: uppercase;
        text-decoration: none;
        font-weight: bold;
        font-size: $font-size-medium;
        margin-bottom: 40px;

        &--selected {
            color: $color-primary;
            font-weight: bold;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__links {
        display: flex;
        margin-top: 100px;
    }

    &__link {
        width: 40px;
        height: 40px;
        font-size: 40px;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }
    }
}