@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;700&display=swap);
*{box-sizing:border-box}html,body{margin:0;padding:0;overflow:hidden}a{scroll-behavior:smooth}input:focus,textarea:focus,select:focus,button:focus{outline:none}.navLink,.link{cursor:pointer;color:#F3F3F3;-webkit-transition:color 200ms ease-out;transition:color 200ms ease-out}.navLink:hover,.link:hover{color:#12A755}body{font-family:"Nunito Sans",sans-serif;color:#F3F3F3}.text__emphasis{font-weight:bold}.text__emphasis--primary{color:#12A755}.menu{position:absolute;z-index:3;color:#12A755}.menu__icon{position:absolute;left:20px;top:20px;cursor:pointer;width:36px;height:36px;font-size:36px;z-index:1}.menu__content{position:absolute;display:flex;flex-direction:column;align-items:center;justify-content:center;left:0;background-color:#232323;width:400px;height:100vh;padding:76px;padding-top:100px;-webkit-transition:left 200ms ease-out;transition:left 200ms ease-out}.menu__content--closed{left:-400px}.menu__navigation{display:flex;flex-direction:column;align-items:center;margin:0 auto}.menu__navLink{text-transform:uppercase;text-decoration:none;font-weight:bold;font-size:20px;margin-bottom:40px}.menu__navLink--selected{color:#12A755;font-weight:bold}.menu__navLink:last-child{margin-bottom:0}.menu__links{display:flex;margin-top:100px}.menu__link{width:40px;height:40px;font-size:40px;margin-right:20px}.menu__link:last-child{margin-right:0}.overlayTexture{position:absolute;width:100vw;height:100vh;z-index:100;background-image:url(https://thomva.github.io/portfolio/static/media/Meso_White.b89c4cd3.svg);mix-blend-mode:soft-light;opacity:.07;background-size:100px;pointer-events:none}.showcaseGroup{display:flex;margin-top:calc(50vh - (100vh - 200px) / 2)}.showcaseGroup>*{margin-right:100px}.showcaseGroup>*:last-child{margin-right:0}.showcase{position:relative;display:flex;justify-content:center;background-color:#232323;z-index:1;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;max-width:calc(100vw)}.showcase__image{width:calc(100vh - 200px);height:calc(100vh - 200px);object-fit:cover}.showcase__index{position:absolute;top:20px;right:20px;color:#12A755;font-weight:bold}.showcase__infoContainer{box-sizing:border-box;height:calc(100vh - 200px - 120px - 60px);margin:60px 100px;width:400px}.showcase__info{display:flex;flex-direction:column;max-height:100%;overflow-y:auto}.showcase__name{font-size:40px;margin-top:0;margin-bottom:60px}.showcase__general{font-size:16px;margin-top:0}.showcase__links{margin-top:20px}.showcase__link{width:40px;height:40px;font-size:40px;margin-right:20px}.showcase__link:last-child{margin-right:0}.switch__container{position:relative;display:flex;align-items:center;background-color:#232323;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:30px;font-weight:bold;font-size:20px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.switch__handle{position:absolute;height:100%;background-color:#12A755;-webkit-transition:width 300ms ease-out, margin 300ms ease-out;transition:width 300ms ease-out, margin 300ms ease-out}.switch__label{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0 20px;z-index:2;color:#12A755;-webkit-transition:color 300ms ease-out;transition:color 300ms ease-out}.switch__label--active{color:#121212}.typer{display:flex;align-items:flex-end;white-space:pre}.homePage{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:100vh}.homePage .horizontalScroll{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:100%}.homePage .horizontalScroll__content{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;-webkit-transition:left 200ms ease-out;transition:left 200ms ease-out}.homePage__introContainer{display:flex;flex-direction:column;justify-content:center;align-items:center;min-width:100vw;height:100vh}.homePage__introParagraph{margin-left:7px;-webkit-transition:opacity 1000ms ease-out;transition:opacity 1000ms ease-out}.homePage__introParagraph--hide{opacity:0}.introTyper{font-size:6.25rem;font-weight:bold;line-height:6rem;text-align:left;width:700px;height:100px;z-index:1}.introTyper .typer__caret{width:14px;height:6.25rem;background-color:#12A755;margin-left:0.6rem;margin-bottom:0.4rem;z-index:1;-webkit-transition:width 1s, height 1s, right 1s;transition:width 1s, height 1s, right 1s}.scrollTrack{position:absolute;height:10px;width:calc(100% - (100vw - 150px) + 50vw);background-color:#232323;top:calc(50vh - 5px);left:calc(100vw - 150px);z-index:0}.scrollTrack__fill{position:absolute;background-color:#12A755;height:100%;left:0;top:0;-webkit-transition:width 200ms ease-out;transition:width 200ms ease-out}.page{background-color:#121212;color:#F3F3F3;min-height:100vh}.layout .switch{position:absolute;top:20px;right:20px}

