.homePage {
	width: fit-content;
	height: 100vh;

	& .horizontalScroll {
		width: fit-content;
		height: 100%;


		&__content {
			display: flex;
			width: fit-content;

			transition: left 200ms ease-out;
		}
	}

	&__introContainer {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		
		min-width: 100vw;
		height: 100vh;
	}

	&__introParagraph {
		margin-left: 7px;

		transition: opacity 1000ms ease-out;

		&--hide {
			opacity: 0;
		}
	}
}

.introTyper {
	font-size: 6.25rem;
	font-weight: bold;
	line-height: 6rem;
	text-align: left;
	width: 700px;
	height: 100px;
	z-index: 1;

	& .typer__caret {
		width: 14px;
		height: 6.25rem;
		background-color: $color-primary;
		margin-left: 0.6rem;
		margin-bottom: 0.4rem;
		z-index: 1;

		transition: width 1s, height 1s, right 1s;
	}
}

.scrollTrack {
	position: absolute;
	height: 10px;
	width: calc(100% - (100vw - 150px) + 50vw);
	background-color: $color-dark-lighter;
	top: calc(50vh - 5px);
	left: calc(100vw - 150px);
	z-index: 0;

	&__fill {
		position: absolute;
		background-color: $color-primary;
		height: 100%;
		left: 0;
		top: 0;
		transition: width 200ms ease-out;
	}
}